<template>
  <div>
    <!---->
    <div class="board">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
        <el-breadcrumb-item>API</el-breadcrumb-item>
        <el-breadcrumb-item>API</el-breadcrumb-item>
      </el-breadcrumb>
    </div>


    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="2.5">
          <el-button type="primary" @click="addDialogVisible = true">API</el-button>
        </el-col>
        <el-col :span="2">
          <el-button type="danger" @click="isAbleMultipleDelete"></el-button>
        </el-col>
      </el-row>

      <el-table :data="roleList" border stripe @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="apiId" label="API" width="145"></el-table-column>
        <el-table-column prop="modelName" label="" width="180"></el-table-column>
        <el-table-column prop="apiKey" label="API KEY" width="180"></el-table-column>
        <el-table-column label="" width="200">
          <template slot-scope="scope">
            <el-tooltip effect="dark" content="API" placement="top" :enterable="false" :open-delay="500">
              <el-button type="primary" icon="el-icon-edit" size="mini"
                         @click="showEditDialog(scope.row.apiId)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="API" placement="top" :enterable="false" :open-delay="500">
              <el-button type="danger" icon="el-icon-delete" size="mini"
                         @click="isAbleDelete(scope.row.apiId)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="API" placement="top" :enterable="false" :open-delay="500">
              <el-button :type="scope.row.apiId === apiId ? 'success' : 'info' " icon="el-icon-s-opportunity" size="mini"
                         @click="getApi(scope.row.apiId)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pageNum"
          :page-sizes="[5, 7, 9]"
          :page-size="queryInfo.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>

    </el-card>

    <!--API-->
    <el-dialog title="API" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
      <!---->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item label="" prop="modelName">
          <el-input v-model="addForm.modelName"></el-input>
        </el-form-item>
        <el-form-item label="API KEY" prop="apiKey">
          <el-input v-model="addForm.apiKey"></el-input>
        </el-form-item>
      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
      <el-button @click="addDialogVisible = false"> </el-button>
      <el-button type="primary" @click="addRole"> </el-button>
      </span>
    </el-dialog>

    <!--API-->
    <el-dialog title="API" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="API" prop="apiId">
          <el-input v-model="editForm.apiId" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="modelName">
          <el-input v-model="editForm.modelName"></el-input>
        </el-form-item>
        <el-form-item label="API KEY" prop="apiKey">
          <el-input v-model="editForm.apiKey"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRole"> </el-button>
      </span>
    </el-dialog>



  </div>
</template>

<script>
import moment from "moment";
export default {
  name: "ApiInfo",
  data() {
    return {





      queryInfo: {
        query: '',
        pageNum: 1,
        pageSize: 7
      },
      roleList: [],
      resourceList: [],
      //
      treeProps: {
        children: 'children',
        label: 'name'
      },
      //
      defKeys: [],
      total: 0,

      addDialogVisible: false,
      //API
      addForm: {
        modelName: '',
        apiKey: ''
      },
      //
      addFormRules: {
        modelName: [
          {required: true, message: '', trigger: 'blur'}
        ],
        apiKey: [
          {required: true, message: 'API KEY', trigger: 'blur'}
        ]
      },
      editDialogVisible: false,
      editForm: {
        apiId: null,
        modelName: '',
        apiKey: ''
      },
      checkAbleId: {},
      editFormRules: {
        modelName: [
          {required: true, message: '', trigger: 'blur'}
        ],
        apiKey: [
          {required: true, message: 'API KEY', trigger: 'blur'}
        ]
      },
      multipleSelection: [],
      //
      setRightDialogVisible: false,

      apiId: 0,
      modelName: "",
      apiKey: ""



    }
  },
  created() {
    this.getRoleList()
    this.getApi(this.$store.state.apiId)
  },
  methods: {


    async getApi(id){
      this.apiId = id
      await axios.get('sysApi/' + id).then(resp => {
        console.log(resp)

        this.modelName =  resp.data.data.modelName
        this.apiKey = resp.data.data.apiKey
        this.CommitToStore(this.apiId, this.modelName, this.apiKey)
      })
    },

    CommitToStore(apiId, modelName, apiKey){
      this.$store.commit('setApiId', apiId)
      this.$store.commit('setModelName', modelName)
      this.$store.commit('setApiKey', apiKey)
    },

    async getRoleList() {
      const {data: res} = await axios.get('sysApi', {params: this.queryInfo})
      this.roleList = res.data;
      this.total = res.total
      this.queryInfo.pageNum = res.pageNum
      this.queryInfo.pageSize = res.pageSize
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getRoleList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getRoleList()
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addRole() {
      const _this = this;
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        //

        axios.defaults.headers.post['Content-Type'] = 'application/json'
        const {data: res} = await axios.post('sysApi', JSON.stringify(_this.addForm))
        if (res.code !== 200) return this.$message.error(res.msg)
        //
        this.addDialogVisible = false
        //
        await this.getRoleList()
        this.$message.success('API')
      })
    },
    // 
    async showEditDialog(id) {
      const _this = this
      await axios.get('sysApi/' + id).then(resp => {
        console.log(resp)
        _this.editForm = resp.data.data
      })
      this.editDialogVisible = true
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // API
    editRole() {
      this.$refs.editFormRef.validate(async valid => {
        const _this = this
        if (!valid) return
        axios.defaults.headers.put['Content-Type'] = 'application/json'
        const {data: res} = await axios.put('sysApi/', JSON.stringify(_this.editForm))
        if (res.code !== 200) return this.$message.error(res.msg)

        this.editDialogVisible = false
        await this.getRoleList()
        this.$message.success('API')
      })
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    // 
    async isAbleMultipleDelete() {
      await this.multipleDelete()
    },
    async multipleDelete() {
      const _this = this
      //
      const resp = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      // , resp"confirm"
      // resp"cancel"
      if (resp === 'cancel') {
        return _this.$message.info('')
      }

      let ids = []
      this.multipleSelection.forEach(data => {
        ids.push(data.apiId)
      })
      let success = true
      await axios.delete('sysApi/' + ids).then(resp => {
        if (resp.data.code !== 200) {
          success = false;
        }
      })
      if (!success) return this.$message.error('')
      await this.getRoleList()
      this.$message.success('')
    },
    async isAbleDelete(id) {
      await this.deleteRoleById(id)
    },
    //id
    async deleteRoleById(apiId) {
      const _this = this
      //
      const resp = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)

      // , resp"confirm"
      // resp"cancel"
      console.log(resp)
      if (resp === 'cancel') {
        return _this.$message.info('')
      }

      let success = true
      await axios.delete('sysApi/' + apiId).then(resp => {
        if (resp.data.code !== 200) {
          success = false
        }
      })
      if (!success) return _this.$message.error('API')
      await this.getRoleList()
      this.$message.success('API')
    },




  }
}
</script>

